<template>
  <div class="album">
    <div class="header" @click="back">
      <img src="../assets/images/reback.png" alt class="auto-img" />
    </div>
    <div class="album-header">
      <div class="album-bg" :style="'backgroundImage: url(' + albumData.blurPicUrl + ')'"></div>
      <div class="album-wrap">
        <div class="album-fl">
          <span class="amhed-flbg"></span>
          <img class="amhed-img auto-img" :src="albumData.blurPicUrl" />
          <span class="amhed-icon">专辑</span>
        </div>
        <div class="album-fr" v-if="albumData.name">
          <h2 class="desc f-brk amhed-tl">{{albumData.name}}</h2>
          <div class="amhed-cnt">
            歌手：
            <span class="amhed-singer">{{albumData.artist.name}}</span>
          </div>
          <div class="amhed-time">
            公司：
            {{albumData.company}}
          </div>
        </div>
      </div>
    </div>
    <div class="album-intro">
      <div class="u-intro">
        <div class="f-brk descp" :class="{fth3:isShowf}">
          <span class="despan" v-for="(item,index) in descriptionData" :key="index">
            <i>{{item}}</i>
            <br />
          </span>
        </div>
        <span class="intro-arrow" :class="{arrowup:isShow}" @click="clickShow"></span>
      </div>
    </div>
    <h3>歌曲列表</h3>
    <ul class="sslist-item">
      <Songsheetlistitem
        v-for="(item,index) in songsList"
        :key="index"
        :item="item"
        :number="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></Songsheetlistitem>
    </ul>
  </div>
</template>

<script>
import "../assets/less/album.less";
import Songsheetlistitem from "../components/Songsheetlistitem";
export default {
  name: "Album",
  components: {
    Songsheetlistitem,
  },
  data() {
    return {
      id: "",
      albumData: [],
      songsList: [],
      isShow: false,
      isShowf: true,
      descriptionData: [],
    };
  },
  created() {
    this.id = this.$route.query.id;
    
    this.getAlbumData(this.id);
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    //获取专辑信息
    getAlbumData(id) {
      this.albumData = [];
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "http://music.kele8.cn/album?id=" + id,
      })
        .then((result) => {
          this.$toast.clear();
          
          if (result.data.code == 200) {
            this.albumData = result.data.album;
            this.songsList = result.data.songs;
            let desArr = result.data.album.description;
            desArr = desArr.split("\n");
            //
            this.descriptionData = desArr;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    clickShow() {
      this.isShow = !this.isShow;
      this.isShowf = !this.isShowf;
    },
  },
};
</script>

<style lang="less" scoped>
</style>